  --clr-brown: #3e2723;
  --clr-white: #4446;
  --clr-glass: #81d4fa;
}

.container-animation {
  position: relative;
  width: 30%;
  height: 30%;
  border: 1px #fff solid;
  border-radius: 4px;
  perspective: 1420px;
  perspective-origin: 0% -25%;
  transform-style: preserve-3d;
}

.ground_top {
  position: absolute;
  top: 380px;
  left: 50%;
  background-color: #388e3c;
  background-color: var(--clr-green);
  width: 280px;
  height: 280px;
  transform: translate(-50%, -50%) rotateX(90deg) rotate(45deg);
}
.ground_front {
  position: absolute;
  top: 380px;
  left: 50%;
  background-image: linear-gradient(#388e3c 20%, #3e2723 80%);
  background-image: linear-gradient(var(--clr-green) 20%, var(--clr-brown) 80%);
  width: 280px;
  height: 20px;
  transform: translateX(-50%) rotateY(-45deg) translateZ(140px);
}
.ground_left {
  position: absolute;
  top: 380px;
  left: 50%;
  background-image: linear-gradient(#388e3c 20%, #3e2723 80%);
  background-image: linear-gradient(var(--clr-green) 20%, var(--clr-brown) 80%);
  width: 280px;
  height: 20px;
  transform: translateX(-50%) rotateY(45deg) translateZ(140px);
}

.building > * {
  position: absolute;
  left: 50%;
  border: 0.5px solid #4446;
  border: 0.5px solid var(--clr-white);
  transform-origin: left top;
  transform-style: preserve-3d;
}
.building > *.windowed::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #81d4fa;
  background-color: var(--clr-glass);
  transform: translateZ(-0.1px);
}
